#card {
    width: 250px;
    height: 500px;
    background-color: #4795c2;
    border-radius: 10px;
}

#card img {
    width: 100%;
    height: 200px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    object-fit: cover;
    margin: 0px;
    background-color: white;
}

#card header {
    font-size: 30px;
    color: #296181;
    text-align: center;
    margin-bottom: 0px;
}

#card p {
    margin-left: 10px;
    margin-right: 10px;
    color: #000;
}